import axios from 'axios';
let host = "http://127.0.0.1:8080"
export default {
  getAllTasks() {
    return axios.get(`${host}/api/tasks`, {
      headers: {
        'Authorization': this.getToken(),
      }
    });
  },
  createTask(task) {
    return axios.post(`${host}/api/tasks`, task, {
      headers: {
        'Authorization': this.getToken(),
      }
    });
  },
  updateTaskByID(id, task) {
    return axios.put(`${host}/api/tasks/${id}`, task, {
      headers: {
        'Authorization': this.getToken(),
      }
    });
  },
  deleteTaskByID(id) {
    return axios.delete(`${host}/api/tasks/${id}`, {
      headers: {
        'Authorization': this.getToken(),
      }
    });
  },
  signup(username, password) {
    return axios.post(`${host}/api/registration`, {
      username: username,
      password: password
    });
  },
  login(username, password) {
    return axios.post(`${host}/api/auth`, {
      username: username,
      password: password
    });
  },
  setToken(token) {
    localStorage.token = token
  },
  getToken() {
    return localStorage.token
  },
  logout(cb) {
    delete localStorage.token
    if (cb) cb()
  },
  loggedIn() {
    return !!localStorage.token
  },
}