<template>
  <v-app id="inspire">
    <v-app id="inspire">
      <v-main>
        <v-container class="fill-height" fluid>
          <v-row align="center" justify="center">
            <v-col cols="12" sm="8" md="4">
              <v-card class="elevation-12">
                <v-toolbar color="primary" dark flat>
                  <v-toolbar-title>Login</v-toolbar-title>
                  <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                  <v-form>
                    <v-text-field
                      label="Username"
                      name="username"
                      v-model="username"
                      prepend-icon="mdi-account"
                      type="text"
                      counter
                      :rules="[rules.required, rules.username]"
                      minlength="4"
                      maxlength="20"
                    ></v-text-field>

                    <v-text-field
                      id="password"
                      label="Password"
                      name="password"
                      v-model="password"
                      prepend-icon="mdi-lock"
                      type="password"
                      :rules="[rules.required, rules.passowrd]"
                      counter
                      minlength="8"
                      maxlength="20"
                    ></v-text-field>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" v-on:click="login">Login</v-btn>
                  <v-btn color="primary" v-on:click="signup">Sign Up</v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
        <v-snackbar
          :color="msgColor"
          rounded="xl"
          v-model="msgSnackbar"
          :timeout="5000"
        >
          {{ msg }}
          <template v-slot:action="{ attrs }">
            <v-btn text v-bind="attrs" v-on:click="msgSnackbar = false">
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-main>
    </v-app>
  </v-app>
</template>

<script>
import auth from "../auth";
export default {
  data() {
    return {
      username: "",
      password: "",
      msg: "",
      msgColor: "success",
      msgSnackbar: false,
      rules: {
        required: (value) => !!value || "Required.",
        username: (value) => (value.length <= 20 && value.length>=4) || "Length should between 4 and 20",
        passowrd: (value) => (value.length <= 20 && value.length>=8) || "Length should between 8 and 20",
      },
    };
  },
  methods: {
    showMsg(msg, isSuccess) {
      if (isSuccess) this.msgColor = "success";
      else this.msgColor = "error";
      this.msgSnackbar = true;
      this.msg = msg;
    },
    hideMsg() {
      this.msgSnackbar = false;
    },
    signup() {
      let self = this;
      auth
        .signup(this.username, this.password)
        .then(function () {
          self.showMsg("Success! You can login now!", true);
        })
        .catch(function (error) {
          self.showMsg(error.response.data, false);
        });
    },
    login() {
      let self = this;
      auth
        .login(this.username, this.password)
        .then(function (response) {
          try {
            auth.setToken(response.data["jwt"]);
            self.$router.replace(
              self.$route.query.redirect || "/task-overview"
            );
          } catch (err) {
            console.log(err);
          }
        })
        .catch(function (error) {
          self.showMsg(error.response.data, false);
        });
    },
  },
};
</script>

<style>
.error {
  color: red;
}
</style>