<template>
  <div class="d-flex flex-no-wrap justify-space-between">
    <v-card
      :color="getColor()"
      class="mx-auto"
      width="300px"
      height="200px"
      dark
      @click.native="showDialog()"
    >
      <v-card-title class="text-h5">
        {{ task.title }}
      </v-card-title>
      <v-card-subtitle>
        expiredAt: {{ getDatetimeString(task.expiredAt) }}
      </v-card-subtitle>
      <v-card-text class="text-h7">
        <div>{{ task.content }}</div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import moment from "moment";
export default {
  name: "TaskCard",
  props: ["task", "taskDialog"],
  created() {},
  methods: {
    getColor() {
      if (this.task.done) {
        return "#455a64";
      } else if (this.task.expiredAt < Date.now()/1000) {
        return "#ff7043";
      } else {
        return "#689f38";
      }
    },
    showDialog() {
      this.taskDialog.show = true;
      this.taskDialog.task.id = this.task.ID;
      this.taskDialog.task.title = this.task.title;
      this.taskDialog.task.content = this.task.content;
      this.taskDialog.task.done = this.task.done;
      this.taskDialog.task.expiredAt = this.task.expiredAt;
    },
    getDatetimeString(unixtime) {
      return moment.unix(unixtime).format("YYYY-MM-DD HH:mm");
    },
  },
};
</script>
