<template>
  <div class="text-center">
    <v-dialog v-model="data.show" width="500">
      <v-card>
        <v-card-title>
          <v-text-field
            label="Title"
            hint="Max 20"
            v-model="data.task.title"
          ></v-text-field>
        </v-card-title>
        <v-card-text class="text-h5 font-weight-bold">
          <v-textarea
            v-model="data.task.content"
            name="input-7-1"
            label="Content"
            hint="Max 200"
          ></v-textarea>
          <v-checkbox
            v-model="data.task.done"
            label="Done"
            color="success"
            hide-details
          ></v-checkbox>
          <v-menu
            ref="datepickerMenu"
            v-model="datepickerMenu"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="pickerDate"
                label="Expiration date"
                prepend-icon="mdi-calendar"
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="pickerDate"
              no-title
              @input="datepickerMenu = false"
            ></v-date-picker>
          </v-menu>

          <v-menu
            ref="menu"
            v-model="timepickerMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="pickerTime"
            transition="scale-transition"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="pickerTime"
                label="Expiration time"
                prepend-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
              ></v-text-field>
            </template>
            <v-time-picker
              format="24hr"
              color="green lighten-1"
              v-if="timepickerMenu"
              v-model="pickerTime"
              full-width
              @click:minute="$refs.menu.save(pickerTime)"
            ></v-time-picker>
          </v-menu>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="text-center" color="green" dark @click="save()"
            >Save</v-btn
          >
          <v-btn
            class="white--text text-center"
            color="red"
            v-bind:disabled="!data.task.id"
            @click="deleteTask()"
          >
            Delete
          </v-btn>
        </v-card-actions>
      </v-card>
      <v-snackbar
        :color="msgColor"
        rounded="xl"
        v-model="msgSnackbar"
        :timeout="5000"
      >
        {{ msg }}
        <template v-slot:action="{ attrs }">
          <v-btn text v-bind="attrs" v-on:click="msgSnackbar = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-dialog>
  </div>
</template>
<script>
import auth from "../auth";
import moment from "moment";
export default {
  name: "TaskCardDialog",
  props: ["data"],
  data() {
    return {
      timepickerMenu: false,
      datepickerMenu: false,
      pickerDate: "",
      pickerTime: "",
      msgColor: "success",
      msgSnackbar: false,
      msg: "",
    };
  },
  updated() {},
  watch: {
    "data.show"() {
      if (this.data.show) {
        if (this.data.task.id) {
          let dateString = moment
            .unix(this.data.task.expiredAt)
            .format("YYYY-MM-DD HH:mm");
          this.setDateTime(dateString);
        } else {
          let dateString = moment().format("YYYY-MM-DD HH:mm");
          this.setDateTime(dateString);
        }
      }
    },
  },
  methods: {
    setDateTime(dateTime) {
      // YYYY-MM-DD-hh-mm
      let dates = dateTime.split(" ");
      this.pickerDate = dates[0];
      this.pickerTime = dates[1];
    },
    showMsg(msg, isSuccess) {
      if (isSuccess) this.msgColor = "success";
      else this.msgColor = "error";
      this.msgSnackbar = true;
      this.msg = msg;
    },
    getExpiredAtUnix(){
      return moment(this.pickerDate+" "+this.pickerTime, "YYYY-MM-DD HH:mm").valueOf()/1000
    },
    save() {
      let task = {
        title: this.data.task.title,
        content: this.data.task.content,
        expiredAt: this.getExpiredAtUnix(),
        done: this.data.task.done,
      };
      if (!this.data.task.id) {
        this.create(task);
      } else {
        this.update(this.data.task.id, task);
      }
    },
    hideMsg() {
      this.msgSnackbar = false;
    },
    create(task) {
      let self = this;
      auth
        .createTask(task)
        .then(() => {
          this.data.show = false;
          self.hideMsg();
        })
        .catch((error) => {
          self.showMsg(error.response.data, false);
        });
    },
    update(id, task) {
      let self = this;
      auth
        .updateTaskByID(id, task)
        .then(() => {
          this.data.show = false;
          self.hideMsg();
        })
        .catch((error) => {
          self.showMsg(error.response.data, false);
        });
    },
    deleteTask() {
      let self = this;
      auth
        .deleteTaskByID(this.data.task.id)
        .then(() => {
          this.data.show = false;
          self.hideMsg();
        })
        .catch((error) => {
          self.showMsg(error.response.data, false);
        });
    },
  },
};
</script>
