<template>
  <v-app>
    <v-main>
      <v-container>
        <h1 v-if="tasks.length == 0">It is empty.</h1>
        <v-row v-else>
          <v-col v-for="(task, idx) in tasks" :key="idx">
            <TaskCard :task="task" :taskDialog="taskDialog"></TaskCard>
          </v-col>
        </v-row>
        <v-speed-dial
          fixed
          v-bind:bottom="true"
          v-bind:right="true"
          direction="top"
          transition="slide-y-reverse-transition"
        >
          <template v-slot:activator>
            <v-btn large color="blue darken-2" dark fab>
              <v-icon> mdi-menu </v-icon>
            </v-btn>
          </template>
          <v-btn
            v-on:click="taskDialog.show = true"
            class="mx-2"
            dark
            fab
            color="green"
          >
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
          <v-btn v-on:click="logout" class="mx-2" fab dark color="red">
            <v-icon dark>mdi-logout</v-icon>
          </v-btn>
        </v-speed-dial>
      </v-container>
      <TaskCardDialog :data="taskDialog"> </TaskCardDialog>
    </v-main>
  </v-app>
</template>
<script>
import auth from "../auth";
import TaskCard from "./TaskCard";
import TaskCardDialog from "./TaskCardDialog";
export default {
  name: "TasksOverview",
  components: { TaskCard, TaskCardDialog },
  data() {
    return {
      taskDialog: {
        task: {
          title: "",
          content: "",
          done: false,
          expiredAt: 0,
        },
        show: false,
      },
      tasks: [],
    };
  },
  watch: {
    "taskDialog.show": function (newValue) {
      if (!newValue) {
        this.getAllTasks();
      }
    },
  },
  mounted() {
    this.getAllTasks();
  },
  methods: {
    getAllTasks() {
      this.resetDialogData();
      let self = this;
      auth
        .getAllTasks()
        .then(function (response) {
          self.tasks = response.data;
        })
        .catch(function () {});
    },
    resetDialogData() {
      this.taskDialog.task.id = undefined;
      this.taskDialog.task.title = "";
      this.taskDialog.task.content = "";
      this.taskDialog.task.done = false;
      this.taskDialog.task.expiredAt = 0;
    },
    logout() {
      auth.logout();
      this.$router.replace(this.$route.query.redirect || "/login");
    },
  },
};
</script>
<style>
.fab-container {
  position: fixed;
  bottom: 0;
  right: 0;
}
</style>
